import config from "../config";

// Funcin simple para manejar logs
const log = (...args) => {
  if (config.enableLogs) {
    console.log(...args);
  }
};

// Funcin para errores (siempre se muestran)
log.error = (...args) => {
  console.error(...args);
};

// Funcin para advertencias
log.warn = (...args) => {
  if (config.enableLogs) {
    console.warn(...args);
  }
};

// Funcin para informacin
log.info = (...args) => {
  if (config.enableLogs) {
    console.info(...args);
  }
};

// Funcin para depuracin
log.debug = (...args) => {
  if (config.enableLogs) {
    console.debug(...args);
  }
};

export default log;
