import developmentConfig from "./development";
import productionConfig from "./production";

// Determinar qu configuracin usar
// Por defecto, usar la configuracin de desarrollo
let config = developmentConfig;

// Funcin para cambiar a la configuracin de produccin
export const useProductionConfig = () => {
  config = productionConfig;

  // Deshabilitar console.log en produccin si los logs estn deshabilitados
  if (!config.enableLogs) {
    console.log = function () {};
  }
};

// Funcin para cambiar a la configuracin de desarrollo
export const useDevelopmentConfig = () => {
  config = developmentConfig;

  // Restaurar console.log si fue sobrescrito
  if (typeof console.originalLog === "function") {
    console.log = console.originalLog;
  }
};

// Exportar la configuracin actual
export default config;
